/* App.css */

body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
}

.App {
  max-width: 600px;
  margin: 50px auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

input {
  width: 96%;
  padding: 10px;
  margin-bottom: 20px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  border: 1px solid #eee;
  border-radius: 4px;
  margin-bottom: 10px;
  padding: 15px;
}

h3 {
  color: #333;
}

p {
  color: #666;
}


.checkbox-container {
  margin-top: 10px;
  margin-bottom: 20px;
}

.checkbox-container label {
  margin-left: 10px;
}

li:hover {
  background-color: #f9f9f9;
  transition: background-color 0.3s ease;
}
button[type="submit"] {
  background-color: #515f6d;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
}

/* Hover effect for the submit button */
button[type="submit"]:hover {
  background-color: #283848;
}

/* Style for the Log In button */
.login-button {
  background-color: #515f6d;
  color: #fff;
  padding: 15px 30px; /* Increase padding to make it larger */
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  font-size: 18px; /* Increase font size for a larger button */
  width: 200px; /* Set a specific width if needed */
  height: 60px; /* Set a specific height if needed */
}

.login-button:hover {
  background-color: #283848;
}

.logout-button {
  background-color: #515f6d;
  color: #fff;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  font-size: 12px;
  position: fixed; 
  top: 20px; 
  right: 20px; 
  z-index: 999;
}

.logout-button:hover {
  background-color: #283848;
}